import { createRouter, createWebHashHistory /*createWebHistory*/ } from "vue-router";
import HomeView from "../views/HomeView.vue";
import LanguageLayout from '@/layouts/LanguageLayout.vue'; 

const routes = [
  {
  path: '/:locale(en|es)',
  component: LanguageLayout,
    children: [
      {
        path: "",
        name: "home",
        component: HomeView,
      },
      {
        path: "mazatlan",
        name: "mazatlan",
        component: () =>
          import("../views/MazatlanView.vue"),
      },
      {
        path: "travel-and-stay",
        name: "travel-and-stay",
        component: () =>
          import("../views/TravelView.vue"),
      },
      {
        path: "rsvp",
        name: "rsvp",
        component: () =>
          import("../views/RsvpView.vue"),
      },
      {
        path: "questions-and-answers",
        name: "questions-and-answers",
        component: () =>
          import("../views/QuestionsAnswers.vue"),
      },
      {
        path: "registry",
        name: "registry",
        component: () =>
        import("../views/RegistryView.vue"),
      },
      {
        path: "wedding-schedule",
        name: "wedding-schedule",
        component: () =>
        import("../views/WeddingScheduleView.vue"),
      },
    ],
  },
{
  path: '/:pathMatch(.*)*',
  redirect: '/en',
}
];

const router = createRouter({
  //history: createWebHistory(process.env.BASE_URL),
    history: createWebHashHistory(),
  routes,
  scrollBehavior(to, from, savedPosition) {
    // always scroll to top
    return savedPosition || {top: 0}
  },
});

export default router;
