<template>
<section class="hero flex-center">
    <div class="hero__container">
        <h1 class="title5">Grecia & Gunnar</h1>
        <p class="date">{{ $t('date') }}</p>
        <p class="rsvp">{{ $t('rsvp') }}</p>
        <CountDown />
        <Button textButton="RSVP" :urlButton="localePath('rsvp')" />
    </div>
</section>
</template>

<script>
import Button from '@/components/buttons/Button.vue'
import CountDown from '@/components/CountDown.vue'

export default {
  name: "HeroMain",
  components: {
    CountDown,
    Button
  },
   computed: {
    currentLocale() {
      return this.$route.params.locale;
    },
  },
  methods: {
    // Construct path using the current locale
    localePath(subPath) {
      return `/${this.currentLocale}/${subPath}`.replace(/\/+$/, ''); // remove trailing slash
    },
  }, 
};
</script>

