<template>
    <header class="header" ref="headerRef" :class="{'header--sticky': onScrollDown}">
      <div class="header__container">
        <MainLogo />
        <MenuIcon  @activeIcon="activeMenu()" :activeClass="isOpen" />
        <div class="menu" :class="{'menu--open': isOpen}">
          <MainNav />
        </div>
      </div>
    </header>
  </template>
  
  <script>
  import MenuIcon from "@/components/MenuIcon.vue";
  import MainNav from "@/components/MainNav.vue";
  import MainLogo from "@/components/MainLogo.vue";

  export default {
    name: "MainHeader",
    components: {
      MenuIcon,
      MainNav,
      MainLogo
    },
    data(){
      return {
        onScrollDown: false,
        isOpen: false,
      }
    },
    watch: {
      '$route'(){
        this.isOpen = false
      }
    },
    methods: {
      stickyMenuOnScroll(){
        const headerRef = this.$refs.headerRef; 
        const sticky =  headerRef.offsetTop;
        
        window.addEventListener("scroll", () => {
          if (window.pageYOffset > sticky) {
            this.onScrollDown = true
          } else {
            this.onScrollDown = false
          }
        });
      },

      activeMenu() {
            this.isOpen = !this.isOpen;
      },
    },
    mounted(){
      this.stickyMenuOnScroll()
    }
  };
  </script>

<style scoped lang="scss">
  .header {
      position: fixed;
      width: 100%;
      z-index: 1000;
      transition: all 0.3s;
      height: 0;
      box-sizing: border-box;
      background: #707171;
