<template>
  <div class="">
    <div class="coundown" v-if="timeLeft">
        <div class="time">
          {{ timeLeft.days }} <span>{{ $t('days') }}</span>
        </div>
        <div class="time">{{ timeLeft.hours }} <span>{{ $t('hours') }}</span>  </div>
        <div class="time">{{ timeLeft.minutes }} <span>{{ $t('minutes') }}</span> </div>
        <div class="time">{{ timeLeft.seconds }} <span>{{ $t('seconds') }}</span> </div>
    </div>
    <div v-else>
      <p>The countdown has ended!</p>
    </div>
  </div>
</template>

<script>
export default {
  name: "CountDown",
  data() {
    return {
      // Specify the target date and adjust it to Mazatlán time
      targetDate: this.getTargetDate(),
      timeLeft: null,
    };
  },
  methods: {
    getTargetDate() {
      // Create the target date in the America/Mazatlan timezone
      const options = { timeZone: "America/Mazatlan", hour12: false };
      const targetDate = new Date(
        "2025-11-22T16:30:00-07:00"
      ).toLocaleString("en-US", options);

      // Convert it back to a timestamp
      return new Date(targetDate).getTime();
    },
    updateCountdown() {
      // Get current time adjusted to Mazatlán timezone
      const now = new Date();
      const gmt7Now = new Date(
        now.toLocaleString("en-US", { timeZone: "America/Mazatlan" })
      ).getTime();

      const distance = this.targetDate - gmt7Now;

      if (distance <= 0) {
        this.timeLeft = null; // Countdown has ended
        clearInterval(this.timer); // Clear interval
        return;
      }

      // Calculate days, hours, minutes, seconds
      this.timeLeft = {
        days: Math.floor(distance / (1000 * 60 * 60 * 24)),
        hours: Math.floor((distance % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60)),
        minutes: Math.floor((distance % (1000 * 60 * 60)) / (1000 * 60)),
        seconds: Math.floor((distance % (1000 * 60)) / 1000),
      };
    },
  },
  mounted() {
    this.updateCountdown(); // Initial call
    this.timer = setInterval(this.updateCountdown, 1000); // Update every second
  },
  beforeUnmount() {
    clearInterval(this.timer); // Clear interval when component is destroyed
  },
};
</script>

