<template>
    <div class="logo">
        <RouterLink class="logo__link" :to="localePath('')">G&G</RouterLink>
    </div>
</template>
<script>
export default {
    name: "MainLogo",
          computed: {
    currentLocale() {
      return this.$route.params.locale;
    },
  },
  methods: {
    // Construct path using the current locale
    localePath(subPath) {
      return `/${this.currentLocale}/${subPath}`.replace(/\/+$/, ''); // remove trailing slash
    },
  },
};
</script>
<style scoped lang="scss">
.logo {
    font-size: 30px;
    z-index: 10;
    font-weight: normal;

    &__link {
          text-decoration: none;
          color: white;
          font-family: "Lora", serif;
              font-size: 40px;
    letter-spacing: 2px;

          &:hover {
            text-decoration: none;
